const router = new VueRouter({
    routes,
    mode: 'history'
})

router.beforeEach((to, from, next) => {
    if (to.meta.isAuth) {
        let token = localStorage.getItem('token')
        let stoken = sessionStorage.getItem('stoken')
        if (to.path === '/login' || token || stoken) {
            next()
        } else {
            next('/login')
        }
    } else {
        next()
    }
    if (to.meta.toTop) {
        window.scrollTo(0, 0)
    } else {
        next()
    }
})

export default router